<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import BeneficiaryService from '@/services/beneficiary/BeneficiaryService'
import CountryLookup from '@/views/nucleus/country/CountryLookup'
import CurrencyLookup from '@/views/nucleus/currency/CurrencyLookup'
import BankLookup from '@/views/bank/bank/BankLookup'
import BranchLookup from '@/views/bank/branch/BranchLookup'
import { FormWizard, TabContent } from 'vue-form-wizard'

export default {
  name: 'beneficiary-modify',
  mixins: [AlertMixin, LoadingMixin],
  components: {
    FormWizard,
    TabContent,
    CountryLookup,
    BankLookup,
    BranchLookup,
    CurrencyLookup
  },
  data: function () {
    return {
      beneficiary: {},
      swiftSelected: false
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      BeneficiaryService.beneficiaries({
        beneficiaryId: this.$route.params.id
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.beneficiary = response.data.beneficiaryList[0] || {}
      })
    },
    selectCountryRecord (record) {
      this.swiftSelected = false
      this.beneficiary.countryGeoId = record.geoId
      this.beneficiary.countryGeoName = record.geoName
    },
    selectBankRecord (record) {
      this.swiftSelected = false
      this.beneficiary.bankId = record.bankId
      this.beneficiary.bankName = record.bankName
    },
    selectSwiftCodeRecord (record) {
      if (Object.keys(record).length === 0) {
        this.swiftSelected = false
        this.beneficiary.countryGeoId = record.countryGeoId || this.beneficiary.countryGeoId
        this.beneficiary.countryGeoName = record.countryName || this.beneficiary.countryGeoName
        this.beneficiary.bankId = record.bankId || this.beneficiary.bankId
        this.beneficiary.bankName = record.bankName || this.beneficiary.bankName
        this.beneficiary.swiftCode = record.swiftCode
        this.beneficiary.city = record.city
      } else {
        this.swiftSelected = true
        this.beneficiary.countryGeoId = record.countryGeoId
        this.beneficiary.countryGeoName = record.countryName
        this.beneficiary.bankId = record.bankId
        this.beneficiary.bankName = record.bankName
        this.beneficiary.swiftCode = record.swiftCode
        this.beneficiary.city = record.city
      }
    },
    selectPreferredCurrencyRecord (record) {
      this.beneficiary.preferredCurrencyId = record.uomId
      this.beneficiary.preferredCurrency = record.description
    },
    modifyBeneficiary () {
      let self = this
      this.startLoading('loading-container')
      BeneficiaryService.modifyBeneficiary({
        beneficiaryId: this.$route.params.id,
        nickname: this.beneficiary.nickname,
        beneficiaryName: this.beneficiary.beneficiaryName,
        accountNumber: this.beneficiary.accountNumber,
        iban: this.beneficiary.iban,
        swiftCode: this.beneficiary.swiftCode,
        preferredCurrencyId: this.beneficiary.preferredCurrencyId,
        address1: this.beneficiary.address1,
        district: this.beneficiary.district,
        city: this.beneficiary.city,
        province: this.beneficiary.province,
        countryGeoId: this.beneficiary.countryGeoId,
        postalCode: this.beneficiary.postalCode,
        postOfficeBox: this.beneficiary.postOfficeBox
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          self.$router.push({ name: 'BrowseBeneficiaries' })
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
